import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import {
  Container, Box, TextField, Button, Typography, Paper,
  Alert, Divider, IconButton, InputAdornment
} from '@mui/material';
import { Visibility, VisibilityOff, Google } from '@mui/icons-material';
import { loginRequest, clearSessionExpired } from '../store/slices/authSlice';
import Background from '../assets/loginBackground.png';
import ThirushiLogo from '../assets/BrandLogo.png';
import PickleTopDown from '../assets/Login_up_down_Image.jpg';

const Login = () => {
  const dispatch = useDispatch();
  const { loading, error, sessionExpired } = useSelector((state) => state.auth);
  
  const [formData, setFormData] = useState({
    username: '',
    password: '',
  });
  const [showPassword, setShowPassword] = useState(false);

  // Clear session expired flag when login component mounts
  useEffect(() => {
    if (sessionExpired) {
      dispatch(clearSessionExpired());
    }
  }, [sessionExpired, dispatch]);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(loginRequest(formData));
  };

  const handleGoogleLogin = () => {
    window.location.href = 'http://localhost:8080/oauth2/authorization/google';
  };

  return (
     <div 
      className="min-h-screen flex items-center justify-center p-4 relative"
      style={{
        backgroundImage: `url(${Background})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
      }}
    >
      {/* Overlay for better contrast */}
      <div 
        className="absolute inset-0 backdrop-blur-sm"
        style={{ zIndex: 0 }}
      />
      
      {/* Top Image - Mobile Only */}
      <Box
        sx={{
          display: { xs: 'block', md: 'none' },
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          height: '120px',
          zIndex: 2,
          overflow: 'hidden',
        }}
      >
        <img 
          src={PickleTopDown}
          alt="Top Decoration"
          style={{
            width: '100%',
            height: '100%',
            objectFit: 'cover',
            opacity: 0.7,
          }}
        />
      </Box>

      {/* Bottom Image - Mobile Only */}
      <Box
        sx={{
          display: { xs: 'block', md: 'none' },
          position: 'absolute',
          bottom: 0,
          left: 0,
          right: 0,
          height: '120px',
          background:"white",
          zIndex: 2,
          overflow: 'hidden',
        }}
      >
        <img 
          src={PickleTopDown}
          alt="Bottom Decoration"
          style={{
            width: '100%',
            height: '100%',
            objectFit: 'cover',
            opacity: 0.8,
          }}
        />
      </Box>
      
      <Container maxWidth="xs" style={{ position: 'relative', zIndex: 3 }}>
        <Paper 
          elevation={24} 
          className="rounded-2xl overflow-hidden"
          style={{
            background: 'rgba(255, 255, 255, 0.95)',
            backdropFilter: 'blur(10px)',
          }}
        >
          {/* Header Section with Gradient */}
          <Box 
            className="p-5 text-center"
            style={{
              background: 'linear-gradient(135deg, #141852 0%, #141852 100%)',
            }}
          >
            <Box 
              className="p-5"
              style={{
                background: 'linear-gradient(135deg, #141852 0%, #141852 100%)',
              }}
            >
              <Box
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  gap: 3,
                }}
              >
                {/* Image on one side */}
                <img
                  src={ThirushiLogo}
                  alt="Welcome"
                  style={{
                    width: 70,
                    height: 70,
                    objectFit: 'contain',
                  }}
                />

                {/* Text on other side */}
                <Box>
                  <Typography 
                    variant="h5"
                    className="font-bold text-white"
                    sx={{
                      textShadow: '2px 2px 4px rgba(0,0,0,0.2)',
                      fontWeight: 700,
                    }}
                  >
                    Welcome Back!
                  </Typography>

                  <Typography 
                    variant="body2"
                    sx={{
                      color: 'rgba(255, 255, 255, 0.95)',
                      fontWeight: 500,
                      marginTop: '4px',
                    }}
                  >
                    Please login to continue
                  </Typography>
                </Box>
              </Box>
            </Box>
          </Box>

          {/* Form Section */}
          <Box className="p-5">
            {/* Show session expired message */}
            {sessionExpired && (
              <Alert 
                severity="warning" 
                className="mb-4 rounded-lg"
                style={{ borderRadius: '12px' }}
              >
                Your session has expired. Please login again.
              </Alert>
            )}

            {/* Show login error */}
            {error && (
              <Alert 
                severity="error" 
                className="mb-4 rounded-lg"
                style={{ borderRadius: '12px' }}
              >
                {error}
              </Alert>
            )}

            <form onSubmit={handleSubmit}>
              <TextField
                fullWidth
                label="Username"
                name="username"
                value={formData.username}
                onChange={handleChange}
                margin="normal"
                required
                variant="outlined"
                sx={{
                  '& .MuiOutlinedInput-root': {
                    borderRadius: '12px',
                    '&:hover fieldset': {
                      borderColor: '#dc2626',
                    },
                    '&.Mui-focused fieldset': {
                      borderColor: '#dc2626',
                    },
                  },
                  '& .MuiInputLabel-root.Mui-focused': {
                    color: '#dc2626',
                  },
                }}
              />
              
              <TextField
                fullWidth
                label="Password"
                name="password"
                type={showPassword ? 'text' : 'password'}
                value={formData.password}
                onChange={handleChange}
                margin="normal"
                required
                variant="outlined"
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton 
                        onClick={() => setShowPassword(!showPassword)}
                        edge="end"
                      >
                        {showPassword ? <VisibilityOff /> : <Visibility />}
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
                sx={{
                  '& .MuiOutlinedInput-root': {
                    borderRadius: '12px',
                    '&:hover fieldset': {
                      borderColor: '#dc2626',
                    },
                    '&.Mui-focused fieldset': {
                      borderColor: '#dc2626',
                    },
                  },
                  '& .MuiInputLabel-root.Mui-focused': {
                    color: '#dc2626',
                  },
                }}
              />

              <Box textAlign="right" mt={1} mb={2}>
                <Link 
                  to="/forgot-password" 
                  style={{ 
                    textDecoration: 'none',
                    color: '#141852',
                    fontSize: '0.875rem',
                    fontWeight: 600,
                    transition: 'all 0.2s',
                  }}
                  onMouseEnter={(e) => e.target.style.textDecoration = 'underline'}
                  onMouseLeave={(e) => e.target.style.textDecoration = 'none'}
                >
                  Forgot Password?
                </Link>
              </Box>

              <Button
                type="submit"
                fullWidth
                variant="contained"
                size="large"
                disabled={loading}
                sx={{
                  mt: 1,
                  py: 1.2,
                  borderRadius: '12px',
                  background: 'linear-gradient(135deg, #141852 0%, #141852 100%)',
                  fontWeight: 600,
                  fontSize: '1rem',
                  textTransform: 'none',
                  boxShadow: '0 4px 14px 0 rgba(220, 38, 38, 0.4)',
                  '&:hover': {
                    background: 'linear-gradient(135deg, #b91c1c 0%, #c2410c 100%)',
                    boxShadow: '0 6px 20px 0 rgba(220, 38, 38, 0.5)',
                  },
                  '&:disabled': {
                    background: '#e5e7eb',
                  },
                }}
              >
                {loading ? 'Logging in...' : 'Login'}
              </Button>
            </form>

            <Divider 
              className="my-4"
              sx={{
                '&::before, &::after': {
                  borderColor: 'rgba(0, 0, 0, 0.12)',
                },
              }}
            >
              <Typography variant="body2" color="blue" sx={{ fontWeight: 500 }}>
                OR
              </Typography>
            </Divider>

            <Box mt={3} textAlign="center">
              <Typography variant="body2" color="textSecondary">
                Don't have an account?{' '}
                <Link 
                  to="/register" 
                  style={{
                    color: '#dc2626',
                    fontWeight: 700,
                    textDecoration: 'none',
                    transition: 'all 0.2s',
                  }}
                  onMouseEnter={(e) => e.target.style.textDecoration = 'underline'}
                  onMouseLeave={(e) => e.target.style.textDecoration = 'none'}
                >
                  Register here
                </Link>
              </Typography>
            </Box>
          </Box>
        </Paper>
      </Container>
    </div>
  );
};

export default Login;